'use client';

import React, { useState } from 'react';
import { Task, Tag } from '@/sample/tasks';
import { TaskItem } from './task-item';
import { TaskForm } from './task-form';
import { Button } from '@/components/ui/button';
import { Plus, Search } from 'lucide-react';

interface TaskListProps {
  tasks: Task[];
  onTaskUpdate: (updatedTask: Task) => void;
  onTaskCreate: (newTask: Partial<Task>) => void;
  className?: string;
}

export function TaskList({ tasks, onTaskUpdate, onTaskCreate, className }: TaskListProps) {
  const [isNewTaskDialogOpen, setIsNewTaskDialogOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterCompleted, setFilterCompleted] = useState<boolean | null>(null);
  
  const handleToggleComplete = (taskId: string, completed: boolean) => {
    const taskToUpdate = tasks.find(t => t.id === taskId);
    if (taskToUpdate) {
      onTaskUpdate({
        ...taskToUpdate,
        completed
      });
    }
  };
  
  const handleRemoveTag = (taskId: string, tagId: string) => {
    const taskToUpdate = tasks.find(t => t.id === taskId);
    if (taskToUpdate) {
      onTaskUpdate({
        ...taskToUpdate,
        tags: taskToUpdate.tags.filter(tag => tag.id !== tagId)
      });
    }
  };
  
  const handleCreateTask = (newTask: Partial<Task>) => {
    onTaskCreate(newTask);
  };
  
  // Filter tasks
  const filteredTasks = tasks.filter(task => {
    // Search query filter
    const matchesSearch = searchQuery === '' || 
      task.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      task.description?.toLowerCase().includes(searchQuery.toLowerCase()) ||
      task.tags.some(tag => tag.name.toLowerCase().includes(searchQuery.toLowerCase()));
    
    // Completion status filter
    const matchesCompletion = filterCompleted === null || task.completed === filterCompleted;
    
    return matchesSearch && matchesCompletion;
  });
  
  return (
    <div className={className}>
      {/* Search and filters */}
      <div className="flex flex-col sm:flex-row gap-2 mb-6">
        <div className="relative flex-grow">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground h-5 w-5" />
          <input
            type="text"
            placeholder="Search tasks..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-10 pr-4 py-2.5 w-full border border-border rounded-md text-base focus:outline-none focus:ring-1 focus:ring-primary"
          />
        </div>
        
        <div className="flex gap-2">
          <select
            value={filterCompleted === null ? 'all' : filterCompleted ? 'completed' : 'incomplete'}
            onChange={(e) => {
              const value = e.target.value;
              setFilterCompleted(
                value === 'all' ? null : value === 'completed'
              );
            }}
            className="px-4 py-2.5 border border-border rounded-md text-base bg-background focus:outline-none focus:ring-1 focus:ring-primary"
          >
            <option value="all">All Tasks</option>
            <option value="completed">Completed</option>
            <option value="incomplete">Incomplete</option>
          </select>
          
          <Button 
            onClick={() => setIsNewTaskDialogOpen(true)} 
            className="text-base py-2.5 px-4"
          >
            <Plus className="h-5 w-5 mr-1.5" />
            New Task
          </Button>
        </div>
      </div>
      
      {/* Task list */}
      <div className="space-y-2">
        {filteredTasks.length > 0 ? (
          filteredTasks.map(task => (
            <TaskItem
              key={task.id}
              task={task}
              onToggleComplete={handleToggleComplete}
              onRemoveTag={handleRemoveTag}
            />
          ))
        ) : (
          <div className="text-center py-10 bg-accent/10 rounded-lg border border-border">
            <p className="text-muted-foreground text-base">
              {searchQuery || filterCompleted !== null
                ? "No matching tasks found"
                : "No tasks available yet. Create a new task to get started."}
            </p>
          </div>
        )}
      </div>
      
      {/* New task dialog */}
      <TaskForm
        open={isNewTaskDialogOpen}
        onOpenChange={setIsNewTaskDialogOpen}
        onSubmit={handleCreateTask}
        mode="create"
      />
    </div>
  );
} 