'use client';

import React, { useState } from 'react';
import { format } from 'date-fns';
import { X, AlertCircle, Clock, Flag, Timer } from 'lucide-react';
import { cn } from '@/lib/utils';
import { Task, Tag } from '@/sample/tasks';
import { TaskDialog } from './task-dialog';

interface TaskItemProps {
  task: Task;
  onToggleComplete: (taskId: string, completed: boolean) => void;
  onRemoveTag: (taskId: string, tagId: string) => void;
}

export function TaskItem({ task, onToggleComplete, onRemoveTag }: TaskItemProps) {
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  
  const handleCheckboxChange = (e: React.MouseEvent) => {
    e.stopPropagation();
    onToggleComplete(task.id, !task.completed);
  };
  
  const handleTagRemove = (tagId: string, e: React.MouseEvent) => {
    e.stopPropagation();
    onRemoveTag(task.id, tagId);
  };
  
  const formatDueDate = (dateString: string | null) => {
    if (!dateString) return null;
    return format(new Date(dateString), 'MMM d, yyyy');
  };
  
  // Get priority color and icon
  const getPriorityDisplay = (priority: Task['priority']) => {
    switch (priority) {
      case 'urgent':
        return { color: 'text-red-500', icon: <AlertCircle size={16} className="mr-1" /> };
      case 'high':
        return { color: 'text-orange-500', icon: <Flag size={16} className="mr-1" /> };
      case 'medium':
        return { color: 'text-yellow-500', icon: <Flag size={16} className="mr-1" /> };
      case 'low':
        return { color: 'text-blue-500', icon: <Flag size={16} className="mr-1" /> };
      default:
        return { color: 'text-muted-foreground', icon: null };
    }
  };
  
  const priorityDisplay = getPriorityDisplay(task.priority);
  const dueDate = formatDueDate(task.dueDate);
  
  return (
    <>
      <div 
        className={cn(
          "flex items-center rounded-lg border border-border p-4 mb-3 gap-3 cursor-pointer hover:border-primary/30 hover:bg-accent/20 transition-all",
          task.completed && "opacity-60",
        )}
        onClick={() => setIsDialogOpen(true)}
      >
        {/* Checkbox */}
        <div 
          className={cn(
            "w-6 h-6 rounded-full border-2 flex items-center justify-center flex-shrink-0",
            task.completed ? "bg-primary border-primary" : "border-muted-foreground"
          )}
          onClick={handleCheckboxChange}
        >
          {task.completed && (
            <svg width="12" height="12" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M8.33334 2.5L3.75001 7.08333L1.66667 5" stroke="white" strokeWidth="1.67" strokeLinecap="round" strokeLinejoin="round"/>
            </svg>
          )}
        </div>
      
        {/* Content */}
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2 mb-1.5">
            <h3 className={cn(
              "text-base font-medium truncate",
              task.completed && "line-through"
            )}>
              {task.title}
            </h3>
            
            <div className={cn("flex items-center text-sm font-medium", priorityDisplay.color)}>
              {priorityDisplay.icon}
              <span className="capitalize">{task.priority}</span>
            </div>
          </div>
          
          {/* Tags */}
          {task.tags.length > 0 && (
            <div className="flex flex-wrap gap-1 mt-1.5">
              {task.tags.map(tag => (
                <div 
                  key={tag.id}
                  className="flex items-center gap-1 text-sm rounded-full px-2.5 py-0.5" 
                  style={{ backgroundColor: `${tag.color}20`, color: tag.color }}
                >
                  <span className="truncate max-w-[100px]">{tag.name}</span>
                  <button 
                    onClick={(e) => handleTagRemove(tag.id, e)}
                    className="hover:bg-black/10 rounded-full p-0.5"
                  >
                    <X size={14} />
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>
        
        {/* Pomodoro Counter */}
        <div className="flex items-center text-sm text-muted-foreground mx-2">
          <Timer size={16} className="mr-1.5" />
          <span className="font-medium">{task.completedPomodoros}/{task.estimatedPomodoros}</span>
        </div>
        
        {/* Due Date */}
        {dueDate && (
          <div className="flex items-center text-sm text-muted-foreground ml-2">
            <Clock size={16} className="mr-1.5" />
            <span><span className="font-medium">Due:</span> {dueDate}</span>
          </div>
        )}
      </div>
      
      <TaskDialog 
        task={task}
        open={isDialogOpen}
        onOpenChange={setIsDialogOpen}
      />
    </>
  );
} 